.error {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.text {
  font-size: 420px;
}

.link {
  text-decoration: none;
  color: #f2f2f3;
  transition: all 0.2s linear;
}

.link:hover {
  opacity: 0.5;
}
